import { Component, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { MessageService } from 'src/app/services/message.service';
import { StorageService } from 'src/app/services/storage.service';
import { ContactDetailComponent } from '../contact-detail/contact-detail.component';
import { AddressComponent } from '../address/address.component';
import { OrganizationSetupService } from 'src/app/services/organization-setup.service';
import { CommonService } from 'src/app/services/common/common.service';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { ConfirmationDialogComponent } from 'src/app/components/shared/confirmation-dialog/confirmation-dialog.component';

@Component({
  selector: 'app-office-create',
  templateUrl: './office-create.component.html',
  styleUrls: ['./office-create.component.scss'],
})
export class OfficeCreateComponent {
  @ViewChild(ContactDetailComponent)
  contactChildComponent!: ContactDetailComponent;
  @ViewChild(AddressComponent) addressComponent!: AddressComponent;

  dialogRef!: MatDialogRef<ConfirmationDialogComponent, any>;

  apiData: any;
  contactData!: FormGroup;
  errorFieldEnable!: any;
  addressData: any = {
    regionalOfficeName: '',
    siteOffice: '',
    address1: '',
    address2: '',
    city: '',
    state: '',
    country: '',
    pin: '',
    lon: '',
    lat: '',
  };
  corporateOfficeId: any;
  countryList: any;
  stateList: any;
  cityList: any;

  constructor(
    private storageService: StorageService,
    private organizationService: OrganizationSetupService,
    private spinner: NgxSpinnerService,
    private messageService: MessageService,
    private route: Router,
    private commonService: CommonService,
    public dialog: MatDialog
  ) {}

  ngOnInit() {
    window.scrollTo(0, 0);
    if (!this.storageService.sendDataToCreateOffice)
      this.route.navigate(['/setup']);
    else {
      this.apiData = this.storageService.sendDataToCreateOffice;
      this.corporateOfficeId =
        this.storageService.sendDataToCreateOffice?.corporateOfficeId;
      this.addressData = this.apiData.addressData;
      this.contactData = this.apiData.contactData
        ? this.apiData.contactData
        : this.contactData;
      if (this.addressData.country) this.getStateList(this.addressData.country);
      if (this.addressData.state) this.getCityList(this.addressData.state);
      if (this.addressData.lat && this.addressData.lon) {
        setTimeout(() => {
          if (this.addressData.lat != 'NaN' || this.addressData.lon != 'NaN')
            this.addressComponent.showLatLong(
              this.addressData?.lat,
              this.addressData?.lon
            );
        }, 0);
      }
    }

    this.getCountryList();
  }

  addNewAddressEntry(contactComponents: any) {
    contactComponents.addTableColumn();
  }

  getCountryList() {
    this.spinner.show();
    this.commonService.getCountryList().subscribe({
      next: (res: any) => {
        this.countryList = res.data;
        this.spinner.hide();
      },
      complete() {},
    });
  }

  getStateList(countryId: any) {
    this.spinner.show();
    this.commonService.getStateList(countryId).subscribe({
      next: (res: any) => {
        this.stateList = res.data;
        this.spinner.hide();
      },
      complete() {},
    });
  }

  getCityList(selectedStateId: any) {
    this.spinner.show();
    this.commonService.getCityList(selectedStateId).subscribe({
      next: (res: any) => {
        this.cityList = res.data;
        this.spinner.hide();
      },
      complete() {},
    });
  }

  getCityStateSelectedList(responseData: any) {
    if (responseData.type == 'country') {
      this.addressData.pin = '';
      this.addressData.city = '';
      this.addressData.state = '';
      this.cityList = [];
      this.stateList = [];
      this.getStateList(responseData.id);
    } else {
      this.addressData.pin = '';
      this.addressData.city = '';
      this.cityList = [];
      this.getCityList(responseData.id);
    }
  }

  validateData(contactComponents: any) {
    this.spinner.show();
    let addressData = this.addressData;
    let contactData = contactComponents;

    if (
      (!this.apiData.isCorporateOffice &&
        !this.apiData.isSiteOffice &&
        !addressData?.regionalOfficeName) ||
      !addressData.address1 ||
      !addressData.country ||
      !addressData.state ||
      !addressData.city ||
      !addressData.pin ||
      !contactData.contactForm.valid ||
      (this.apiData.isSiteOffice &&
        (!addressData.siteOffice ||
          !addressData.projectTypeId ||
          !addressData.regionalOfficeId ||
          !addressData.installedCapacity))
    ) {
      this.errorFieldEnable = {
        address1: addressData.address1 ? false : true,
        country: addressData.country ? false : true,
        state: addressData.state ? false : true,
        city: addressData.city ? false : true,
        pin: addressData.pin ? false : true,
        regionalOfficeName:
          !this.apiData.isSiteOffice && !addressData?.regionalOfficeName
            ? true
            : false,
        projectTypeId:
          this.apiData.isSiteOffice && !addressData.projectTypeId
            ? true
            : false,
        installedCapacity:
          this.apiData.isSiteOffice && !addressData.installedCapacity
            ? true
            : false,
        regionalOfficeId:
          this.apiData.isSiteOffice && !addressData.regionalOfficeId
            ? true
            : false,
        siteOffice:
          this.apiData.isSiteOffice && !addressData?.siteOffice ? true : false,
      };
      if (
        (!this.apiData.isCorporateOffice &&
          !this.apiData.isSiteOffice &&
          !addressData?.regionalOfficeName) ||
        !addressData.address1 ||
        !addressData.country ||
        !addressData.state ||
        !addressData.city ||
        !addressData.pin ||
        (this.apiData.isSiteOffice &&
          (!addressData.siteOffice ||
            !addressData.projectTypeId ||
            !addressData.installedCapacity ||
            !addressData.regionalOfficeId))
      ) {
        this.messageService.errorToast('Please fill all required fields.');
        contactData.checkError(false);
      } else contactData.checkError(true);
      this.spinner.hide();
    } else if (addressData.pin.length != 6) {
      this.messageService.errorToast('Please enter 6 digit pin code.');
      this.errorFieldEnable = {
        pin: true,
      };
      this.spinner.hide();
    } else {
      this.errorFieldEnable = null;
      this.spinner.hide();
      this.confirmationDialog('submit', addressData, contactData);
    }
  }

  submitRegionalData(addressData: any, contactComponents: any) {
    let contactForm = contactComponents.getContactData();
    this.spinner.show();
    let requestBody = {
      regionalOfficeName: addressData.regionalOfficeName,
      croporateOfficeId: this.corporateOfficeId,
      addressLine1: addressData.address1,
      addressLine2: addressData.address2,
      countryId: Number(addressData.country),
      stateId: Number(addressData.state),
      cityId: Number(addressData.city),
      pinCode: addressData.pin,
      longitude: `${addressData.lon}`,
      latitude: `${addressData.lat}`,
      isActive: true,
      officeContactPeople: contactForm.value,
    };
    this.organizationService.submitRegionalDetails(requestBody).subscribe({
      next: (res: any) => {
        this.messageService.successToast(
          `${addressData.regionalOfficeName} created successfully`
        );
        this.renderToSite();
      },
    });
  }

  updateRegionalData(addressData: any, contactComponents: any) {
    let contactForm = contactComponents.getContactData();
    this.spinner.show();
    let requestBody = {
      regionalOfficeName: addressData.regionalOfficeName,
      id: addressData.id,
      croporateOfficeId: this.corporateOfficeId,
      addressLine1: addressData.address1,
      addressLine2: addressData.address2,
      countryId: Number(addressData.country),
      stateId: Number(addressData.state),
      cityId: Number(addressData.city),
      pinCode: addressData.pin,
      longitude: `${addressData.lon}`,
      latitude: `${addressData.lat}`,
      isActive: addressData.isActive,
      officeContactPeople: contactForm.value,
    };
    this.organizationService.updateRegionalDetails(requestBody).subscribe({
      next: (res: any) => {
        this.messageService.successToast(
          `${addressData.regionalOfficeName} updated successfully`
        );
        this.renderToSite();
      },
    });
  }

  deleteRegionalOffice() {
    this.spinner.show();
    this.organizationService
      .deleteRegionalDetails(this.addressData.id)
      .subscribe((res: any) => {
        this.spinner.hide();
        this.messageService.successToast(
          `${this.addressData.regionalOfficeName} deleted successfully`
        );
        this.renderToSite();
      });
  }

  submitSiteOfficeData(addressData: any, contactComponents: any) {
    let contactForm = contactComponents.getContactData();
    this.spinner.show();
    let requestBody = {
      corporateOfficeId: this.corporateOfficeId,
      regionalOfficeId: addressData.regionalOfficeId,
      siteOffice: addressData.siteOffice,
      addressLine1: addressData.address1,
      addressLine2: addressData.address2,
      countryId: Number(addressData.country),
      stateId: Number(addressData.state),
      cityId: Number(addressData.city),
      pinCode: addressData.pin,
      latitude: `${addressData.lat}`,
      longitude: `${addressData.lon}`,
      detail: addressData.detail,
      isActive: true,
      installedCapacity: addressData.installedCapacity,
      projectTypeId: addressData.projectTypeId,
      officeContactPeople: contactForm.value,
    };
    this.organizationService
      .submitSiteOfficeDetails(requestBody)
      .subscribe((res: any) => {
        this.messageService.successToast(
          `${addressData.siteOffice} created successfully`
        );
        this.renderToSite();
      });
  }

  updateSiteData(addressData: any, contactComponents: any) {
    let contactForm = contactComponents.getContactData();
    this.spinner.show();
    let requestBody = {
      id: addressData.id,
      corporateOfficeId: this.corporateOfficeId,
      regionalOfficeId: addressData.regionalOfficeId,
      siteOffice: addressData.siteOffice,
      detail: addressData.detail,
      installedCapacity: addressData.installedCapacity,
      projectTypeId: addressData.projectTypeId,
      regionalOfficeName: addressData.regionalOfficeName,
      addressLine1: addressData.address1,
      addressLine2: addressData.address2,
      countryId: Number(addressData.country),
      stateId: Number(addressData.state),
      cityId: Number(addressData.city),
      pinCode: addressData.pin,
      longitude: `${addressData.lon}`,
      latitude: `${addressData.lat}`,
      isActive: addressData.isActive,
      officeContactPeople: contactForm.value,
    };
    this.organizationService
      .updateSiteOfficeDetails(requestBody)
      .subscribe((res: any) => {
        this.messageService.successToast(
          `${addressData.siteOffice} updated successfully`
        );
        this.renderToSite();
      });
  }

  deleteSiteOffice() {
    this.spinner.show();
    this.organizationService
      .deleteSiteOffice(this.addressData.id)
      .subscribe((res: any) => {
        this.messageService.successToast(
          `${this.addressData.siteOffice} deleted successfully`
        );
        this.renderToSite();
      });
  }

  renderToSite() {
    this.storageService.sendDataToCreateOffice = null;
    this.storageService.previousTab = {
      button: this.apiData.isCorporateOffice
        ? 'button1'
        : this.apiData.isSiteOffice
          ? 'button3'
          : 'button2',
      corporateOfficeId: this.corporateOfficeId,
    };
    this.route.navigate(['/setup']);
    this.spinner.hide();
  }

  confirmationDialog(
    headerType: string,
    addressData: any,
    contactComponents: any
  ) {
    this.dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      width: '24.514vw',
      height: '12.083vw',
      data: {
        header: headerType == 'submit' ? 'Save ?' : 'Delete ?',
        data: {
          bodyData:
            headerType == 'submit'
              ? 'Are you sure, you want to submit the changes?'
              : `Are you sure, you want to Delete this ${this.apiData?.isSiteOffice ? 'Site' : 'Regional'} Office ?`,
        },
        button: {
          isSubmit: headerType == 'submit' ? true : false,
          isCancel: true,
          isDelete: headerType == 'delete' ? true : false,
        },
      },
    });
    this.dialogRef.disableClose = true;

    this.dialogRef.afterClosed().subscribe(result => {
      if (result == 'ok') {
        if (headerType == 'submit') {
          if (!this.apiData.isEdit) {
            if (!this.apiData?.isSiteOffice)
              this.submitRegionalData(addressData, contactComponents);
            else this.submitSiteOfficeData(addressData, contactComponents);
          } else {
            if (!this.apiData?.isCorporateOffice && !this.apiData?.isSiteOffice)
              this.updateRegionalData(addressData, contactComponents);
            else if (this.apiData?.isSiteOffice)
              this.updateSiteData(addressData, contactComponents);
            else this.updateOrganizationData();
          }
        } else if (!this.apiData?.isSiteOffice) {
          this.deleteRegionalOffice();
        } else {
          this.deleteSiteOffice();
        }
      }
    });
  }

  updateOrganizationData() {
    this.spinner.show();
    let requestBody = {
      id: this.corporateOfficeId,
      organizationName: this.apiData?.organizationName,
      logo: this.apiData.logo,
      updatedLogo: this.apiData?.updatedLogo,
      addressLine1: this.addressData.address1,
      addressLine2: this.addressData.address2,
      countryId: Number(this.addressData.country),
      stateId: Number(this.addressData.state),
      cityId: Number(this.addressData.city),
      pinCode: this.addressData.pin,
      longitude: `${this.addressData.lon}`,
      latitude: `${this.addressData.lat}`,
      reportingFrequencyId: this.apiData?.reportingFrequencyId,
      dataCapturingFrequencyId: this.apiData?.reportingFrequencyId,
      reportingFrequencyStartMonth: this.apiData?.reportingFrequencyStartMonth,
      officeContactPeople:
        this.contactChildComponent.contactForm.value.tableData,
      corporateOfficeIndustryTypes: this.apiData?.selectedItem,
    };
    this.organizationService.updateCorporateDetails(requestBody).subscribe({
      next: (res: any) => {
        this.messageService.successToast('Data update successFully');
        this.renderToSite();
        this.spinner.hide();
      },
    });
  }
}
