import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class StorageService {
  constructor() {}

  private _sendDataToCreateOffice: any;
  private _previousTab: any;

  public get sendDataToCreateOffice(): any {
    return this._sendDataToCreateOffice;
  }
  public set sendDataToCreateOffice(value: any) {
    this._sendDataToCreateOffice = value;
  }

  public get previousTab(): any {
    return this._previousTab;
  }
  public set previousTab(value: any) {
    this._previousTab = value;
  }
}
