import {
  Component,
  ViewEncapsulation,
  ViewChild,
  ChangeDetectorRef,
} from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ContactDetailComponent } from './contact-detail/contact-detail.component';
import { OrganizationSetupService } from 'src/app/services/organization-setup.service';
import { MessageService } from 'src/app/services/message.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { AddressComponent } from './address/address.component';
import { CommonService } from 'src/app/services/common/common.service';
import { StorageService } from 'src/app/services/storage.service';
import { Router } from '@angular/router';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { ConfirmationDialogComponent } from 'src/app/components/shared/confirmation-dialog/confirmation-dialog.component';
import { environment } from '../../../../environments/environment';

@Component({
  selector: 'app-setup',
  templateUrl: './setup.component.html',
  styleUrls: ['./setup.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class SetupComponent {
  @ViewChild(ContactDetailComponent)
  contactChildComponent!: ContactDetailComponent;
  @ViewChild(AddressComponent) addressComponent!: AddressComponent;
  dialogRef!: MatDialogRef<ConfirmationDialogComponent, any>;

  button1: boolean = true;
  button3!: boolean;
  button2!: boolean;
  imageUrl!: any;
  newDepartmentName!: any;
  selectedItems: any = [];
  dropdownSettings = {
    // dropdown setting
    singleSelection: false,
    idField: 'id', // dropdown id name
    textField: 'indstryType', // dropdown value name
    selectAllText: 'Select All',
    unSelectAllText: 'Reset All',
    itemsShowLimit: 2,
  };
  isModalOpen!: boolean;
  addressData: any = {
    address1: '',
    address2: '',
    city: '',
    state: '',
    country: '',
    pin: '',
    lon: '',
    lat: '',
  };
  addressData2: any = {
    address1: '',
    address2: '',
    regionalOfficeName: '',
    city: '',
    state: '',
    country: '',
    pin: '',
    lon: '',
    lat: '',
  };
  addressData3: any = {
    address1: '',
    address2: '',
    siteOffice: '',
    city: '',
    state: '',
    country: '',
    pin: '',
    lon: '',
    lat: '',
    installedCapacity: '',
    projectTypeId: undefined,
    detail: '',
    isActive: true,
    regionalOfficeId: undefined,
  };
  contactData!: FormGroup;
  contactData2!: FormGroup;
  contactData3!: FormGroup;
  siteOfficeData: any = [];
  regionalOfficeData: any = [];
  departmentList: any = [];
  industryTypeList: any = [];
  frequencyList: any = [];
  organizationName: any;
  reportingFrequencyId!: any;
  reportingFrequencyStartMonth: any = '';
  reportingPeriodList!: any;
  isUpdateButtonEnable!: boolean;
  selectedItem2: any = [];
  updatedLogo: any;
  showLogoUrl!: any;
  corporateOfficeId: any;
  countryList: any = [];
  stateList: any = [];
  cityList: any = [];
  regionalList: any;
  projectList: any;
  userTypeList: any;
  designationList: any = [];
  isRegionalAccordionEnable: { [key: number]: boolean } = {};
  isSiteAccordionOpen: { [key: number]: boolean } = {};
  previousRegionalIndex!: any;
  previousSiteIndex!: any;
  errorFieldEnable!: any;
  completeDataPercentage!: any;
  isNameDisabled!: boolean;
  completeOrganizationData!: any;
  isCorporateDisabled: boolean = false;
  isSiteTabEnable!: boolean;

  constructor(
    private changeDetector: ChangeDetectorRef,
    private organizationService: OrganizationSetupService,
    private commonService: CommonService,
    private storageService: StorageService,
    private messageService: MessageService,
    private spinner: NgxSpinnerService,
    private route: Router,
    public dialog: MatDialog
  ) {}

  ngOnInit() {
    this.getDepartmentList();
    this.getIndustryTypeList();
    this.getFrequency();
    this.getCountryList();
    this.getAllUserType();
    this.getDesignationList();
    if (this.storageService.previousTab) {
      if (this.storageService.previousTab.button == 'button2') {
        this.button1 = false;
        this.button2 = true;
      } else if (this.storageService.previousTab.button == 'button3') {
        this.button1 = false;
        this.button3 = true;
      }
      this.corporateOfficeId =
        this.storageService?.previousTab?.corporateOfficeId;
      this.tabChange('ts');
      this.storageService.previousTab = null;
    }
  }

  onSelectFile(event: any) {
    const file = event.target.files[0];
    if (this.errorFieldEnable && this.errorFieldEnable['image']) {
      this.errorFieldEnable['image'] = false;
    }
    if (file) {
      // Check if a file is selected
      const fileSizeInKB = file.size / 1024;
      if (fileSizeInKB >= 51) {
        this.imageUrl = null;
        this.showLogoUrl = null;
        return this.messageService.errorToast(
          'Please upload image size less then 50kb'
        );
      }
      const reader = new FileReader();
      reader.onload = (e: any) => {
        if (
          this.completeOrganizationData?.organizationName ||
          this.completeOrganizationData?.logo
        ) {
          this.showLogoUrl = e.target.result;
          this.updatedLogo = e.target.result;
          this.updateFinalData();
        } else {
          this.imageUrl = e.target.result;
          this.showLogoUrl = e.target.result;
        }
      };
      reader.readAsDataURL(file);
    }
  }

  trimSpace(event: any) {
    const input = event.target.value;
    event.target.value = input.replace(/[^a-zA-Z0-9\s]/g, '').trimStart();
  }

  trimSpaceModel(event: any) {
    const input = event.target.value;
    event.target.value = input.replace(/[^a-zA-Z\s]/g, '').trimStart();
  }

  addNewAddressEntry(contactComponents: any) {
    contactComponents.addTableColumn();
  }

  tabChange(type: string) {
    this.isCorporateDisabled = false;
    this.errorFieldEnable = null;
    this.regionalOfficeData = [];
    if (this.button1) {
      this.isRegionalAccordionEnable[this.previousRegionalIndex] = false;
      this.isSiteAccordionOpen[this.previousSiteIndex] = false;
      this.button1 = true;
      this.button2 = false;
      this.button3 = false;
      if (type == 'UI') this.getOrganizedDetail();
      setTimeout(() => {
        this.addressComponent.ngOnInit();
        if (this.addressData.lat != 'NaN' || this.addressData.lon != 'NaN')
          this.addressComponent.showLatLong(
            this.addressData.lat,
            this.addressData.lon
          );
      }, 0);
    } else if (this.button2) {
      this.getRegionalOfficeListById();
      this.isRegionalAccordionEnable[this.previousRegionalIndex] = false;
      this.isSiteAccordionOpen[this.previousSiteIndex] = false;
      this.previousRegionalIndex = undefined;
      this.button1 = false;
      this.button2 = true;
      this.button3 = false;
    } else if (this.button3) {
      this.isRegionalAccordionEnable[this.previousRegionalIndex] = false;
      this.isSiteAccordionOpen[this.previousSiteIndex] = false;
      this.previousSiteIndex = undefined;
      this.getSiteOffice();
      this.button1 = false;
      this.button2 = false;
      this.button3 = true;
    }
  }

  getPercentage() {
    this.spinner.show();
    this.organizationService.getPercentage().subscribe((res: any) => {
      this.completeDataPercentage = res.data;
      this.spinner.hide();
    });
  }

  regionalAccordion(data: any, index: any) {
    this.errorFieldEnable = null;
    if (this.previousRegionalIndex >= 0) {
      this.isRegionalAccordionEnable[this.previousRegionalIndex] = false;
      this.previousRegionalIndex = index;
    } else {
      this.previousRegionalIndex = index;
    }
    this.isRegionalAccordionEnable[index] =
      !this.isRegionalAccordionEnable[index];
    if (data.addressData.country) this.getStateList(data.addressData.country);
    if (data.addressData.state) this.getCityList(data.addressData.state);
    if (this.isRegionalAccordionEnable[index])
      setTimeout(() => {
        if (data.addressData.lat != 'NaN' || this.addressData.lon != 'NaN')
          this.addressComponent.showLatLong(
            data?.addressData?.lat,
            data?.addressData?.lon
          );
      }, 0);
  }

  siteAccordion(data: any, index: any) {
    this.errorFieldEnable = null;
    if (this.previousSiteIndex >= 0) {
      this.isSiteAccordionOpen[this.previousSiteIndex] = false;
      this.previousSiteIndex = index;
    } else {
      this.previousSiteIndex = index;
    }
    this.isSiteAccordionOpen[index] = !this.isRegionalAccordionEnable[index];
    if (data.addressData.country) this.getStateList(data.addressData.country);
    if (data.addressData.state) this.getCityList(data.addressData.state);
    if (this.isSiteAccordionOpen[index])
      setTimeout(() => {
        if (data.addressData.lat != 'NaN' || this.addressData.lon != 'NaN')
          this.addressComponent.showLatLong(
            data?.addressData?.lat,
            data?.addressData?.lon
          );
      }, 0);
  }

  getCountryList() {
    this.countryList = [];
    this.commonService.getCountryList().subscribe({
      next: (res: any) => {
        this.countryList = res.data;
        this.changeDetector.detectChanges();
      },
      error: err => {
        console.log(err);
      },
      complete() {},
    });
  }

  getStateList(countryId: any) {
    this.stateList = [];
    this.commonService.getStateList(countryId).subscribe({
      next: (res: any) => {
        this.stateList = res.data;
        this.changeDetector.detectChanges();
      },
      error: err => {
        console.log(err);
      },
      complete() {},
    });
  }

  getCityList(selectedStateId: any) {
    this.cityList = [];
    this.commonService.getCityList(selectedStateId).subscribe({
      next: (res: any) => {
        this.cityList = res.data;
        this.changeDetector.detectChanges();
      },
      error: err => {
        console.log(err);
      },
      complete() {},
    });
  }

  getCityStateSelectedList(responseData: any) {
    if (responseData.type == 'country') {
      this.stateList = [];
      this.cityList = [];
      this.addressData.state = '';
      this.addressData.city = '';
      this.addressData.pin = '';
      this.getStateList(responseData.id);
    } else {
      this.cityList = [];
      this.addressData.city = '';
      this.addressData.pin = '';
      this.getCityList(responseData.id);
    }
  }

  getDepartmentList() {
    this.spinner.show();
    this.departmentList = [];
    this.organizationService.getListOfDepartment().subscribe((res: any) => {
      this.departmentList = res.data;
      this.spinner.hide();
    });
  }

  getIndustryTypeList() {
    this.spinner.show();
    this.organizationService.getListOfIndustryType().subscribe(
      (res: any) => {
        this.industryTypeList = res.data.filter((ele: any) => ele.isActive);
        this.spinner.hide();
        this.getOrganizedDetail();
      },
      (error: any) => {
        this.getOrganizedDetail();
      }
    );
  }

  getFrequency() {
    this.spinner.show();
    this.organizationService
      .getAllReportingFrequency()
      .subscribe((res: any) => {
        this.frequencyList = res.data;
        this.spinner.hide();
      });
  }

  getReportingPeriodById(event: any, type: string) {
    if (event?.target?.value || event) {
      this.spinner.show();
      this.reportingPeriodList = [];
      if (type == 'UI') this.reportingFrequencyStartMonth = '';
      this.organizationService
        .getReportingFrequencyById(
          Number(type == 'UI' ? event.target.value : event)
        )
        .subscribe((res: any) => {
          this.reportingPeriodList = res.data;
          this.spinner.hide();
        });
    }
  }

  getAllUserType() {
    this.organizationService.getAllUserType().subscribe((res: any) => {
      this.userTypeList = res.data;
    });
  }

  getDesignationList() {
    this.departmentList = [];
    this.organizationService.getDesignationList().subscribe((res: any) => {
      this.designationList = res.data;
    });
  }

  submitFinalData() {
    this.selectedItem2 = [];
    this.industryTypeList.forEach((data: any) => {
      const matchData = { id: data.id, indstryType: data.indstryType };
      this.selectedItem2.push({
        id: 0,
        corporateOfficeId: 0,
        isActive:
          JSON.stringify(
            this.selectedItems.find((ele: any) => ele.id == data.id)
          ) == JSON.stringify(matchData),
        industryTypeId: data.id,
      });
    });
    this.spinner.show();
    let requestBody = {
      organizationName: this.organizationName,
      logo: this.imageUrl?.split('base64,')[1],
      addressLine1: this.addressData.address1,
      addressLine2: this.addressData.address2,
      countryId: Number(this.addressData.country),
      stateId: Number(this.addressData.state),
      cityId: Number(this.addressData.city),
      pinCode: this.addressData.pin,
      longitude: `${this.addressData.lon}`,
      latitude: `${this.addressData.lat}`,
      reportingFrequencyId: Number(this.reportingFrequencyId),
      dataCapturingFrequencyId: Number(this.reportingFrequencyId),
      reportingFrequencyStartMonth: Number(this.reportingFrequencyStartMonth),
      officeContactPeople:
        this.contactChildComponent.contactForm.value.tableData,
      corporateOfficeIndustryTypes: this.selectedItem2,
    };
    this.organizationService.submitCorporateDetails(requestBody).subscribe({
      next: (res: any) => {
        this.messageService.successToast('Data submitted successFully');
        this.getOrganizedDetail();
        this.spinner.hide();
      },
    });
  }

  updateFinalData() {
    this.selectedItem2 = [];
    this.industryTypeList.forEach((data: any) => {
      const matchData = { id: data.id, indstryType: data.indstryType };
      this.selectedItem2.push({
        id: 0,
        corporateOfficeId: 0,
        isActive:
          JSON.stringify(
            this.selectedItems.find((ele: any) => ele.id == data.id)
          ) == JSON.stringify(matchData),
        industryTypeId: data.id,
      });
    });
    this.spinner.show();
    let requestBody = {
      id: this.corporateOfficeId,
      organizationName: this.organizationName,
      logo: this.completeOrganizationData?.logo?.includes('base64,')
        ? this.completeOrganizationData?.logo?.split('base64,')[1]
        : this.completeOrganizationData?.logo,
      updatedLogo: this.updatedLogo?.includes('base64,')
        ? this.updatedLogo?.split('base64,')[1]
        : this.updatedLogo,
      addressLine1: this.addressData.address1,
      addressLine2: this.addressData.address2,
      countryId: Number(this.addressData.country),
      stateId: Number(this.addressData.state),
      cityId: Number(this.addressData.city),
      pinCode: this.addressData.pin,
      longitude: `${this.addressData.lon}`,
      latitude: `${this.addressData.lat}`,
      reportingFrequencyId: Number(this.reportingFrequencyId),
      dataCapturingFrequencyId: Number(this.reportingFrequencyId),
      reportingFrequencyStartMonth: Number(this.reportingFrequencyStartMonth),
      officeContactPeople: this.contactData,
      corporateOfficeIndustryTypes: this.selectedItem2,
    };
    this.organizationService.updateCorporateDetails(requestBody).subscribe({
      next: (res: any) => {
        this.messageService.successToast('Data update successFully');
        this.getOrganizedDetail();
        this.spinner.hide();
      },
      error: (err: any) => {
        this.messageService.errorToast(err.error.errorMessage);
        this.spinner.hide();
      },
    });
  }

  getOrganizedDetail() {
    this.getPercentage();
    this.spinner.show();
    this.selectedItems = [];
    this.organizationService.getCorporateDetails().subscribe(
      (res: any) => {
        var arr: any = [];
        this.completeOrganizationData = res.data;
        if (res.data.countryId) this.getStateList(res.data.countryId);
        if (res.data.stateId) this.getCityList(res.data.stateId);
        res.data?.corporateOfficeIndustryTypes.forEach((ele: any) => {
          this.industryTypeList.forEach((filterData: any) => {
            if (filterData.id == ele.industryTypeId && ele.isActive) {
              arr.push({
                id: filterData.id,
                indstryType: filterData.indstryType,
              });
              this.selectedItems = arr;
              this.isNameDisabled = true;
            }
          });
        });

        this.isUpdateButtonEnable = res.data ? true : false;
        this.contactData = res.data.officeContactPeople;
        this.organizationName = res.data.organizationName;
        this.reportingFrequencyId = res.data.dataCapturingFrequencyId;
        this.reportingFrequencyStartMonth =
          res.data.reportingFrequencyStartMonth;
        this.imageUrl = environment.apiHostName + '/' + res?.data?.logo;
        if (res?.data?.logo) localStorage.setItem('imageUrl', this.imageUrl);
        this.showLogoUrl = environment.apiHostName + '/' + res?.data?.logo;
        this.addressData.address1 = res.data.addressLine1;
        if (res.data.addressLine1) this.isCorporateDisabled = true;
        this.addressData.address2 = res.data.addressLine2;
        this.addressData.country = res.data.countryId;
        this.addressData.city = res.data.cityId;
        this.addressData.state = res.data.stateId;
        this.addressData.pin = res.data.pinCode;
        this.addressData.lon = res.data.longitude;
        this.addressData.lat = res.data.latitude;
        this.corporateOfficeId = res.data.id;
        this.getReportingPeriodById(
          res.data.reportingFrequencyStartMonth,
          'api'
        );
        this.changeDetector.detectChanges();
        if (res?.data?.latitude != 'NaN' || res?.data?.longitude != 'NaN')
          this.addressComponent?.showLatLong(
            res?.data?.latitude,
            res?.data?.longitude
          );
        this.contactChildComponent.ngOnInit();
      },
      (error: any) => {
        this.isUpdateButtonEnable = false;
      }
    );
  }

  getRegionalOfficeListById() {
    this.spinner.show();
    this.regionalOfficeData = [];
    this.organizationService
      .getRegionalOfficeListById(this.corporateOfficeId)
      .subscribe((result: any) => {
        this.isSiteTabEnable = result?.data ? true : false;
        result?.data?.forEach((ele: any) => {
          let address = {
            id: ele.id,
            regionalOfficeName: ele.regionalOfficeName,
            address1: ele.addressLine1,
            address2: ele.addressLine2,
            country: ele.countryId,
            state: ele.stateId,
            city: ele.cityId,
            pin: ele.pinCode,
            lon: ele.longitude,
            lat: ele.latitude,
            detail: ele.detail,
            isActive: ele.isActive,
          };
          this.regionalOfficeData.push({
            addressData: address,
            contactData: ele.officeContactPeople,
          });
        });
        this.spinner.hide();
      });
  }

  getSiteOffice() {
    this.spinner.show();
    this.siteOfficeData = [];
    this.getRegionalList();
    this.organizationService
      .getSiteOfficeDetails(this.corporateOfficeId)
      .subscribe((res: any) => {
        res.data.forEach((ele: any) => {
          let address = {
            id: ele.id,
            regionalOfficeId: ele.regionalOfficeId,
            address1: ele.addressLine1,
            address2: ele.addressLine2,
            country: ele.countryId,
            state: ele.stateId,
            city: ele.cityId,
            pin: ele.pinCode,
            lon: ele.longitude,
            lat: ele.latitude,
            detail: ele.detail,
            isActive: ele.isActive,
            corporateOfficeId: ele.corporateOfficeId,
            installedCapacity: ele.installedCapacity,
            projectTypeId: ele.projectTypeId,
            siteOffice: ele.siteOffice,
          };
          this.siteOfficeData.push({
            addressData: address,
            contactData: ele.officeContactPeople,
          });
        });
        this.spinner.hide();
      });
  }

  getRegionalList() {
    this.spinner.show();
    this.getProjectList();
    this.organizationService
      .getRegionalOfficeListById(this.corporateOfficeId)
      .subscribe((res: any) => {
        this.regionalList = res.data;
        this.spinner.hide();
      });
  }

  getProjectList() {
    this.spinner.show();
    this.organizationService.getProjectList().subscribe((res: any) => {
      this.projectList = res.data;
      this.spinner.hide();
    });
  }

  validateData(
    selectedAddressData: any,
    selectedContactData: any,
    redirectOn: string
  ) {
    this.spinner.show();
    let addressData = this.addressData;
    let contactData = this.contactChildComponent;

    if (
      (this.button1 && !this.organizationName) ||
      (this.button1 && this.selectedItems.length === 0) ||
      (this.button1 && !this.reportingFrequencyId) ||
      (this.button1 && !this.reportingFrequencyStartMonth) ||
      !addressData.address1 ||
      !addressData.country ||
      !addressData.state ||
      !addressData.city ||
      !addressData.pin ||
      !contactData.contactForm.valid
    ) {
      this.errorFieldEnable = {
        organizationName: this.organizationName ? false : true,
        industryType: this.selectedItems.length != 0 ? false : true,
        dataCapturingFrequency: this.reportingFrequencyId ? false : true,
        startOfReportingPeriodMonth: this.reportingFrequencyStartMonth
          ? false
          : true,
        address1: addressData.address1 ? false : true,
        country: addressData.country ? false : true,
        state: addressData.state ? false : true,
        city: addressData.city ? false : true,
        pin: addressData.pin ? false : true,
      };

      if (
        !this.organizationName ||
        this.selectedItems.length == 0 ||
        !this.reportingFrequencyId ||
        !this.reportingFrequencyStartMonth ||
        !addressData.address1 ||
        !addressData.country ||
        !addressData.state ||
        !addressData.city ||
        !addressData.pin
      ) {
        this.messageService.errorToast('Please fill all required fields.');
        contactData.checkError(false);
      } else contactData.checkError(true);
      this.spinner.hide();
    } else if (addressData.pin.length != 6) {
      this.messageService.errorToast('Please enter 6 digit pin code.');
      this.errorFieldEnable = {
        pin: true,
      };
      this.spinner.hide();
    } else {
      this.errorFieldEnable = null;
      this.spinner.hide();
      this.confirmationDialog();
    }
  }

  confirmationDialog() {
    this.dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      width: '24.514vw',
      height: '12.083vw',
      data: {
        header: 'Save ?',
        data: { bodyData: `Are you sure, you want to submit the changes?` },
        button: {
          isSubmit: true,
          isCancel: true,
          isDelete: false,
        },
      },
    });
    this.dialogRef.disableClose = true;

    this.dialogRef.afterClosed().subscribe(result => {
      if (result == 'ok') {
        this.submitFinalData();
      }
    });
  }

  enableEdit() {
    this.selectedItem2 = [];
    this.industryTypeList.forEach((data: any) => {
      const matchData = { id: data.id, indstryType: data.indstryType };
      this.selectedItem2.push({
        id: 0,
        corporateOfficeId: 0,
        isActive:
          JSON.stringify(
            this.selectedItems.find((ele: any) => ele.id == data.id)
          ) == JSON.stringify(matchData),
        industryTypeId: data.id,
      });
    });
    this.sendDataToOfficeCreate(true, this.addressData, this.contactData, 0);
  }

  removeError(fieldName: any): void {
    if (this.errorFieldEnable && this.errorFieldEnable[fieldName]) {
      this.errorFieldEnable[fieldName] = false;
    }
  }

  openDepartmentModel(event: any, modal: any) {
    if (event.target.value == 'addDepartment') {
      modal.show();
      event.target.value = null;
    }
    event.target.value = null;
  }

  addDepartmentList(modal: any) {
    if (!this.newDepartmentName) {
      this.errorFieldEnable = {
        newDepartmentName: true,
      };
      return this.messageService.errorToast('Please enter department name');
    }
    this.spinner.show();
    let requestBody = {
      id: 0,
      department: this.newDepartmentName,
      isActive: true,
    };
    this.organizationService
      .addDepartment(requestBody)
      .subscribe((res: any) => {
        this.spinner.hide();
        modal.hide();
        this.newDepartmentName = undefined;
        this.getDepartmentList();
      });
  }

  editAccordionData(
    event: any,
    isEdit: boolean,
    addressData: any,
    contactData: any,
    index: any
  ) {
    this.spinner.show();
    event.stopPropagation();
    this.sendDataToOfficeCreate(isEdit, addressData, contactData, index);
  }

  sendDataToOfficeCreate(
    isEdit: boolean,
    addressData: any,
    contactData: any,
    index: any
  ) {
    this.spinner.show();
    this.storageService.sendDataToCreateOffice = {
      countryList: this.countryList,
      cityList: this.cityList,
      stateList: this.stateList,
      departmentList: this.departmentList,
      userTypeList: this.userTypeList,
      designationList: this.designationList,
      regionalList: this.regionalList,
      projectList: this.projectList,
      isEdit: isEdit,
      isSiteOffice: this.button3,
      isRegionalOffice: this.button2,
      isCorporateOffice: this.button1,
      corporateOfficeId: this.corporateOfficeId,
      selectedItem: this.selectedItem2,
      reportingFrequencyId: Number(this.reportingFrequencyId),
      dataCapturingFrequencyId: Number(this.reportingFrequencyId),
      reportingFrequencyStartMonth: Number(this.reportingFrequencyStartMonth),
      organizationName: this.organizationName,
      logo: this.imageUrl?.includes('base64,')
        ? this.imageUrl?.split('base64,')[1]
        : this.imageUrl,
      updatedLogo: this.updatedLogo?.includes('base64,')
        ? this.updatedLogo?.split('base64,')[1]
        : this.updatedLogo,
      addressData: addressData
        ? addressData
        : this.button2
          ? this.addressData2
          : this.addressData3,
      contactData: contactData ? contactData : null,
      index: index,
    };
    this.route.navigate(['office/create']);
    this.spinner.hide();
  }
}
