import { Component, OnDestroy } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { AddUserComponent } from '../add-user/add-user.component';
import { NgxSpinnerService } from 'ngx-spinner';
import { MessageService } from 'src/app/services/message.service';
import { UserManagementService } from 'src/app/services/user-management.service';
import { PaginationInstance } from 'ngx-pagination';
import { commonData } from '../../shared/commonData';
import { OrganizationSetupService } from 'src/app/services/organization-setup.service';
import { ConfirmationDialogComponent } from 'src/app/components/shared/confirmation-dialog/confirmation-dialog.component';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-user-management',
  templateUrl: './user-management.component.html',
  styleUrls: ['./user-management.component.scss'],
})
export class UserManagementComponent implements OnDestroy {
  dialogRef!: MatDialogRef<AddUserComponent, any>;
  userDataList!: any;
  userList!: any;
  dataSearch!: any;
  userData = {};
  pageSize = 0;
  currentPage = 0;
  Math: any = Math;

  dialogRef1!: MatDialogRef<ConfirmationDialogComponent>;
  tableHeaderArray = [
    { name: 'S.No', width: '6%' },
    { name: 'Name', width: '10%' },
    { name: 'Email Address', width: '17%' },
    { name: 'Contact No.', width: '13%' },
    { name: 'Role', width: '12%' },
    { name: 'Location', width: '19%' },
    { name: 'Outside User', width: '9%' },
    { name: 'Active/ InActive', width: '10%' },

    {
      name: '',
      width: '4%',
    },
  ];
  userListLength: any;

  constructor(
    public dialog: MatDialog,
    private spinner: NgxSpinnerService,
    private messageService: MessageService,
    private userManagementService: UserManagementService,
    private _dialog: MatDialog,
    private _commonData: commonData
  ) {}
  ngOnDestroy(): void {
    if (this.dialogRef) this.dialogRef.close();
  }

  ngOnInit(): void {
    this.getUsersList();
    this.userData = this.createUserDataFormat('');
    this.pageSize = this._commonData.pageSize;
    this.currentPage = this._commonData.currentPage;
  }

  createUserDataFormat(data: any) {
    return {
      id: data?.id ? data?.id : 0,
      userId: data?.userId ? data?.userId : 0,
      firstName: data?.name ? data?.name : '',
      middleName: '',
      lastName: '',
      emailId: data?.email ? data?.email : '',
      phoneNumber: data?.mobile ? data?.mobile : '',
      employeeId: data?.employeeId ? data?.employeeId : '',
      locationId: data?.locationId ? data?.locationId : null,
      userTypeId: data?.userTypeId ? data?.userTypeId : 0,
      designationId: data?.designationId ? data?.designationId : 0,
      departmentId: data?.departmentId ? data?.departmentId : 0,
      photo: data?.photo ? environment.apiHostName + '/' + data.photo : '',
      updatedPhoto: '',
      corporateOfficeId: data?.corporateOfficeId
        ? data?.corporateOfficeId
        : null,
      regionalOfficeId: data?.regionalOfficeId ? data?.regionalOfficeId : null,
      siteOfficeId: data?.siteOfficeId ? data?.siteOfficeId : null,
      isOutsideUser: data?.isOutsideUser ? data?.isOutsideUser : false,
    };
  }

  getUsersList() {
    this.dataSearch = '';
    this.spinner.show();
    this.userManagementService.getUsersList().subscribe((res: any) => {
      this.userList = res.data;
      this.userListLength = res.data.length;
      this.userDataList = res.data;
      this.spinner.hide();
    });
  }

  filterProducts(): void {
    this.currentPage = 1;

    if (this.dataSearch.trim() !== '') {
      this.userList = this.userDataList?.filter((product: any) => {
        let outsideUser = product.isOutsideUser ? 'True' : 'False';
        if (
          product?.name?.toLowerCase()?.includes(this.dataSearch.toLowerCase())
        )
          return product?.name
            ?.toLowerCase()
            ?.includes(this.dataSearch.toLowerCase());
        else if (
          product?.email?.toLowerCase()?.includes(this.dataSearch.toLowerCase())
        )
          return product?.email
            ?.toLowerCase()
            ?.includes(this.dataSearch.toLowerCase());
        else if (
          product?.mobile
            ?.toLowerCase()
            ?.includes(this.dataSearch.toLowerCase())
        )
          return product?.mobile
            ?.toLowerCase()
            ?.includes(this.dataSearch.toLowerCase());
        else if (
          product?.userType
            ?.toLowerCase()
            ?.includes(this.dataSearch.toLowerCase())
        )
          return product?.userType
            ?.toLowerCase()
            ?.includes(this.dataSearch.toLowerCase());
        else if (
          outsideUser?.toLowerCase()?.includes(this.dataSearch.toLowerCase())
        )
          return outsideUser
            ?.toLowerCase()
            ?.includes(this.dataSearch.toLowerCase());
        else
          return product?.location
            ?.toLowerCase()
            ?.includes(this.dataSearch.toLowerCase());
      });
      this.userListLength = this.userList.length;
    } else {
      // If the search term is empty, show all products
      this.userList = this.userDataList;
      this.userListLength = this.userList.length;
    }
  }

  openDialog(type: string, data: any) {
    let requestData;
    if (type == 'edit') {
      requestData = this.createUserDataFormat(data);
    } else requestData = data;
    this.dialogRef = this.dialog.open(AddUserComponent, {
      width: '70%',
      // height: '410px',
      data: {
        header: type == 'edit' ? 'Edit User' : 'Add User',
        data: requestData,
        tab: 'user',
      },
    });
    this.dialogRef.disableClose = true;

    this.dialogRef.afterClosed().subscribe(result => {
      if (result == 'ok') {
        this.getUsersList();
      }
      this.userData = this.createUserDataFormat('');
    });
  }

  changeToggleData(event: any, data: any, index: any) {
    // const confirmation = confirm(
    //   `Are you sure you want to ${event.target.checked ? 'activate' : 'deactivate'} this user?`,
    // );
    // if (!confirmation) {
    //   event.target.checked = !event.target.checked;
    //   return;
    // }
    // console.log(event.target.checked);
    this.spinner.show();
    this.userManagementService
      .isUserActive(event.target.checked, data.email)
      .subscribe(
        (res: any) => {
          // this.messageService.successToast("")
          this.userList[index].isActive = event.target.checked;
          this.userDataList[index].isActive = event.target.checked;
          this.spinner.hide();
        },
        (error: any) => {
          this.spinner.hide();
          this.userList[index].isActive = !event.target.checked;
          this.userDataList[index].isActive = !event.target.checked;
        }
      );
  }

  confirmationDialog(event: any, data: any, index: any) {
    const action = event.target.checked ? 'activate' : 'deactivate';
    const action1 = event.target.checked ? 'Activate User' : 'Deactivate User';

    this.dialogRef1 = this.dialog.open(ConfirmationDialogComponent, {
      width: '24.514vw', //353px
      height: '12.083vw', //174px
      data: {
        header: action1,
        data: {
          bodyData: `Are you sure you want to ${action} this user?`,
        },
        button: {
          isSubmit: true,
          isCancel: true,
          isDelete: false,
        },
      },
    });
    this.dialogRef1.disableClose = true;
    this.dialogRef1.afterClosed().subscribe(result => {
      if (result == 'ok') {
        this.changeToggleData(event, data, index);
      } else {
        data.isActive = !data.isActive;
      }
    });
  }

  pageChange(event: any) {
    console.log(event);
  }
}
