import { Component } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { AddUserComponent } from '../add-user/add-user.component';
import { UserAccessManagementService } from 'src/app/services/user-access-management.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { MessageService } from 'src/app/services/message.service';
import { commonData } from '../../shared/commonData';
interface Item {
  id: number;
  name: string;
}

@Component({
  selector: 'app-access-management',
  templateUrl: './access-management.component.html',
  styleUrls: ['./access-management.component.scss'],
})
export class AccessManagementComponent {
  dialogRef!: MatDialogRef<AddUserComponent, any>;
  userDataList!: any;
  userList!: any;
  dataSearch!: any;
  pageSize = this._commonData.pageSize;
  currentPage = this._commonData.currentPage;
  Math: any = Math;

  tableHeaderArray = [
    { name: 'S.No', width: '5%' },
    { name: 'Name', width: '15%' },
    { name: 'Email Address', width: '20%' },
    { name: ' Role', width: '15%' },
    {
      name: 'Access Level',
      width: '37%',
    },
    {
      name: '',
      width: '5%',
    },
  ];
  userListLength: any;

  constructor(
    public dialog: MatDialog,
    private userAccessmanagement: UserAccessManagementService,
    private spinner: NgxSpinnerService,
    private messageService: MessageService,
    private _commonData: commonData
  ) {}

  ngOnInit(): void {
    this.getUsersAccessManagementList();
  }

  getUsersAccessManagementList() {
    this.dataSearch = '';
    this.spinner.show();
    this.userAccessmanagement
      .getUsersAccessManagementList()
      .subscribe((res: any) => {
        this.userList = res.data;
        this.userListLength = res.data.length;
        this.userDataList = res.data;
        this.spinner.hide();
      });
  }

  filterProducts(): void {
    this.currentPage = 1;
    if (this.dataSearch?.trim() !== '') {
      this.userList = this.userDataList?.filter((product: any) => {
        if (product.name.toLowerCase().includes(this.dataSearch?.toLowerCase()))
          return product.name
            .toLowerCase()
            .includes(this.dataSearch?.toLowerCase());
        else if (
          product.emailAddress
            .toLowerCase()
            .includes(this.dataSearch?.toLowerCase())
        )
          return product.emailAddress
            .toLowerCase()
            .includes(this.dataSearch?.toLowerCase());
        else if (
          product.accessLevel
            .toLowerCase()
            .includes(this.dataSearch?.toLowerCase())
        )
          return product.accessLevel
            .toLowerCase()
            .includes(this.dataSearch?.toLowerCase());
        else if (
          product.role?.toLowerCase()?.includes(this.dataSearch?.toLowerCase())
        )
          return product.role
            ?.toLowerCase()
            ?.includes(this.dataSearch?.toLowerCase());
        else
          return product.location
            ?.toLowerCase()
            ?.includes(this.dataSearch.toLowerCase());
      });
      this.userListLength = this.userList?.length;
    } else {
      // If the search term is empty, show all products
      this.userList = this.userDataList;
      this.userListLength = this.userList?.length;
    }
  }

  openDialog(data: any, type: any) {
    const desiredItemId = data;
    const userDetails = this.userList.find(
      (item: Item) => item.id === desiredItemId
    );

    this.dialogRef = this.dialog.open(AddUserComponent, {
      width: '80%',
      height: '34.722vw', //500px
      data: {
        header: 'User Access',
        data: userDetails,
        tab: 'Access',
      },
    });
    this.dialogRef.disableClose = true;

    this.dialogRef.afterClosed().subscribe(result => {
      if (result == 'ok') {
        this.getUsersAccessManagementList();
      }
    });
  }
}
