import { NgModule } from '@angular/core';
import { CommonModule, DatePipe } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { UserManagementComponent } from './user-management/user-management.component';
import { AccessManagementComponent } from './access-management/access-management.component';
import { SharedModule } from '../shared/shared.module';
import { SetupComponent } from './setup/setup.component';
import { AddressComponent } from './setup/address/address.component';
import { ContactDetailComponent } from './setup/contact-detail/contact-detail.component';
// import { authGuard } from 'src/app/route/auth.guard';

import { OfficeCreateComponent } from './setup/office-create/office-create.component';
import { authGuard } from 'src/app/guards/auth.guard';
const routes: Routes = [
  {
    path: 'setup',
    component: SetupComponent,
    data: { role: ['SuperAdmin', 'Admin'] },
    canActivate: [authGuard],
  },
  {
    path: 'office/create',
    component: OfficeCreateComponent,
    data: { role: ['SuperAdmin', 'Admin'] },
    canActivate: [authGuard],
  },
  {
    path: 'office/edit',
    component: OfficeCreateComponent,
    data: { role: ['SuperAdmin', 'Admin'] },
    canActivate: [authGuard],
  },
  {
    path: 'access-management',
    component: AccessManagementComponent,
    data: { role: ['SuperAdmin', 'Admin'] },
    canActivate: [authGuard],
  },
  {
    path: 'user-management',
    component: UserManagementComponent,
    data: { role: ['SuperAdmin', 'Admin'] },
    canActivate: [authGuard],
  },
];

@NgModule({
  declarations: [
    SetupComponent,
    UserManagementComponent,
    AccessManagementComponent,
    AddressComponent,
    ContactDetailComponent,
    OfficeCreateComponent,
  ],
  imports: [CommonModule, RouterModule.forChild(routes), SharedModule],
  providers: [DatePipe],
})
export class OrganizationModule {}
